
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import {orderDetail, backOrder} from '../config'
export default {
  components: {
    Group,
    Cell,
    Flexbox,
    FlexboxItem,
    XButton,
    Divider
  },
  data () {
    return {
      id: 0,
      order: {},
      orderDetail: JSON.parse(this.$localStorage.get('orderDetail')),
      form: {
        userId: '',
        orderId: ''
      }
    }
  },
  created () {
    this.form.orderId = this.$route.params.orderId
    this.form.userId = this.$route.params.userId
    this.getDetail()
  },
  methods: {
    handleBack () {
      this.$http({
        method: 'jsonp',
        url: backOrder,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '20em',
            position: 'bottom',
            text: '',
            time: '1000'
          })
        }
      })
    },
    changeStatus (num) {
      switch (num) {
        case 0:
          return {
            icon: 'icon-tishi',
            status: '',
            text: '<span style="color:#EB3D00;padding:0 5px;">1</span>'
          }
        case 1:
          return {
            icon: 'icon-faild',
            status: '',
            text: ''
          }
        case 2:
          return {
            icon: 'icon-time',
            status: '',
            text: ''
          }
        case 3:
          return {
            icon: 'icon-pay',
            status: '',
            text: ''
          }
        case 4:
          return {
            icon: 'icon-success',
            status: '',
            text: ''
          }
        case 5:
          return {
            icon: 'icon-faild',
            status: '',
            text: ''
          }
      }
    },
    jump (url) {
      this.$router.push(url)
    },
    getDetail () {
      this.$http({
        method: 'jsonp',
        url: orderDetail,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        this.order = res.body.data.order
        console.log(res)
        this.$localStorage.set('order', JSON.stringify(this.order))
      })
    }
  }
}



